@import "../../style.scss";

.profile-clubs{
    width: 880px;
    box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
    border-radius: 10px;
    margin-top: 30px;
    border-color: rgb(164, 164, 164); 

    display: flex;
    overflow-x: scroll; 
    overflow-y: hidden; 
    white-space: nowrap; /* Prevent wrapping of items */

    /* Optional: Adjust the padding/margin as needed */
    padding: 20px;
    margin-bottom: 50px;;

    /* Add scrollbar styling if needed */
    scrollbar-width: 20px;;
    scrollbar-color: #494949;

    margin-left: auto;
    margin-right: auto;

    .club{
        display: inline-block;
    }
}
 